// Format of the strings: a * x^n + b * y^m

/*

Guidelines for the user (add them onto the website):
Please input your guess as "a * x^n + b * y^m", where a, n, b, m are integers in the range [-5, 5],
or simply a, n, m, b as 4 numbers: the inferred guess will be "a * x^n + b * y^m".
If the coeficient is 1 or 0, please explicitly input it into the console, do not omit it.

*/

// (a) * x^(n) + (b) * y^(m)
//

export function parse_first_integer(string) {
    let value, sign_minus = false;
    let ind = 0;
    if (string[ind] == '-') {
        sign_minus = true;
        ind += 1;
    }
    string = string.slice(ind, string.length);
    value = parseInt(string);
    if (sign_minus) {
        value = -value;
    }

    string = string.slice(string.indexOf(value.toString()) + (value.toString()).length, string.length);
    return [value, string];
}

export function parseGuess(guess) {

    let input = guess;
    let coef = []; // in the order of: a, n, b, m

    let index = 0;
    let string = input;
    while (string != "") {
        let cur = parse_first_integer(string.slice(index, string.length));
        if (Number.isNaN(cur[0]))
            string = string.slice(1, string.length);
        else {
            coef.push(cur[0]);
            string = cur[1];
        }
    }

    if (coef.length != 5) {
        alert("The inputted string is not in the format asked!"); // or raise an error/warning
    }

    let data_structure_element = [[coef[0], coef[1]], [coef[2], coef[3]], coef[4]]

    console.log(data_structure_element[0])
    console.log(data_structure_element[1])
    console.log(data_structure_element[2])

    return data_structure_element
}