import { StatusBar } from 'expo-status-bar';
import { Button, StyleSheet, Text, TextInput, View } from 'react-native';
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, ScatterChart, Scatter, Legend } from 'recharts'
import Plotly from 'react-native-plotly';
import { useEffect, useState } from 'react';
import { genData, generateEquation } from './GenerateData';
import { ToastProvider } from 'react-native-toast-notifications'
import { parseGuess } from './parser';


export default function App() {
  const [data, setData] = useState([])
  const [equation, setEquation] = useState([])
  const [guess, setGuess] = useState('')
  const [guessNumber, setGuessNumber] = useState(0)
  const [levelCurves, setLevelCurves] = useState([0])

  // create an array of hex values of colors
  const colors = ["#FFCF00", "#aecfdf", "#93748a", "#e94f37", "#2f4b26"]

  useEffect(() => {
    let tempEquation = generateEquation()
    setEquation(tempEquation)
    let tempData = genData(tempEquation, guessNumber)
    setData(tempData)
    // if all data values are NaN, then generate new equation
    while (tempData.every((item) => isNaN(item.z=0))) {
      tempEquation = generateEquation()
      setEquation(tempEquation)
      tempData = genData(tempEquation, guessNumber)
      setData(tempData)
    }
  }, [])

  const submitGuess = () => {
    let parsedGuess = parseGuess(guess)
    if (equation.toString() == parsedGuess.toString()){
      alert('Correct!', 'You guessed the equation correctly!')
    }
    else {
      alert('Incorrect!', 'We drew another level curve for you to try again!')

      //pick a number between -5 and 5
      let randomNum = Math.floor(Math.random() * 11) - 5

      let newData = genData(equation, randomNum)
      let tempData = [...data, ...newData]

      let tempCurves = [...levelCurves, randomNum]
      setLevelCurves(tempCurves)

      // if two entries have the same x value then have that entry include both the y0 and y1 values
      tempData = tempData.reduce((unique, o) => {
        if(!unique.some(obj => obj.x === o.x)) {
          unique.push(o);
        } else {
          let index = unique.findIndex(obj => obj.x === o.x)
          unique[index] = {...unique[index], ...o}
        }
        return unique;
      },[]);
      
      setData(tempData)
      setGuessNumber(randomNum)
      console.log(tempData)
    }
  }


  return (
    <ToastProvider>
    <View style={styles.container}>
      <Text style={{fontSize: 30}}>Welcome to Curvdle</Text>
      <Text style={{fontSize: 16, marginTop: 10}}>Guess the equation of the function below:</Text>
      
      <View style={{marginTop: 50}}></View>

      <View style={{flexDirection: 'row'}}>
        <TextInput value={guess} onChangeText={e => setGuess(e)} style={{borderBottomWidth: 1, paddingBottom: 5, width: 250}} placeholder='Enter your guess...' placeholderTextColor="#808080"></TextInput>
        <View style={{marginLeft: 30}}>
          <Button title='Submit' color="#6ca965" onPress={() => submitGuess()}></Button>
        </View>
      </View>


      <View style={{marginTop: 50}}></View>
      
      <LineChart width={400} height={300} data={data} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="x" />
        <YAxis />
        <Tooltip />
        <Legend />

        {
          levelCurves.map((item, index) => {
            return <Line type="monotone" dataKey={`z=${item}`} strokeWidth={0} stroke={colors[index]} dot={{stroke: colors[index], strokeWidth: 0.5, strokeDasharray: ""}} fill='none'/>
          })
        }

      </LineChart>
    </View>
    </ToastProvider>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
