export function genData(equation, z_val){
    let data = []
    if(equation[1][1] == 2){
        for (let i = -5; i <= 5; i += 0.05) {
            let xVar = i
            let colName = `z=${z_val}`
            // account for positive and negative sqrt
            let yVar = Math.pow((- equation[2] - equation[0][0] * Math.pow(xVar, equation[0][1]) + z_val)/equation[1][0], 1/equation[1][1])
            data.push({'x': xVar, [colName]: yVar})
            data.push({'x': xVar, [colName]: -yVar})
        } 

    }
    else{
        for (let i = -5; i <= 5; i += 0.05) {
            let xVar = i
            let colName = `z=${z_val}`
            // account for positive and negative sqrt
            let yVar = Math.pow((- equation[2] - equation[0][0] * Math.pow(xVar, equation[0][1]) + z_val)/equation[1][0], 1/equation[1][1])
            data.push({'x': xVar, [colName]: yVar})
          }
    }
    
  return data
}

export function generateEquation() {
    var x_coeff = Math.floor(Math.random() * 4) - 2;
    var x_pow = Math.floor(Math.random() * 2) + 1;
    var y_coeff = Math.floor(Math.random() * 4) - 2;
    // make sure x and y coeff are not 0
    while (x_coeff == 0) {
        x_coeff = Math.floor(Math.random() * 10) - 5;
    }
    while (y_coeff == 0) {
        y_coeff = Math.floor(Math.random() * 10) - 5;
    }
    var y_pow = Math.floor(Math.random() * 2) + 1;
    var cons_coeff = Math.floor(Math.random() * 10) - 5;

    var output_string = `${x_coeff}x^${x_pow} + ${y_coeff}y^${y_pow} + ${cons_coeff}`;
    console.log(output_string);
    // return an array that contains x_coeff and x_pow as tuples, y_coeff and y_pow as tuples, and cons_coeff as a single value
    return [[x_coeff, x_pow], [y_coeff, y_pow], cons_coeff];
}